.app {
  width: 100%;
  min-height: 100vh;
  background-image: url('/bg.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

@media (min-width: 769px) {
  .app {
    background-attachment: fixed;
  }
}

.container {
  padding: 2.5rem 3rem;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  max-width: 600px;
  width: auto;
  margin: 0 auto;
}

h1 {
  font-size: 3.5rem;
  font-weight: 300;
  color: #fff;
  text-align: center;
  font-family: 'Medodica', 'Cinzel', serif;
  letter-spacing: 0.15em;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
  line-height: 1.1;
  text-transform: uppercase;
  font-style: normal;
  margin: 0;
  padding: 0;
}

@media (max-width: 768px) {
  .app {
    padding: 1.5rem;
  }

  .container {
    padding: 2rem 2.5rem;
    border-radius: 12px;
    max-width: 90%;
  }

  h1 {
    font-size: 2rem;
    letter-spacing: 0.1em;
    line-height: 1.2;
  }
}

@media (max-width: 480px) {
  .app {
    padding: 1rem;
  }

  .container {
    padding: 1.5rem 2rem;
    border-radius: 10px;
    max-width: 90%;
  }

  h1 {
    font-size: 1.5rem;
    letter-spacing: 0.08em;
  }
}

@media (min-width: 1200px) {
  h1 {
    font-size: 4.5rem;
  }
}

